import React, { Component } from 'react'
import logo from './watch_2.jpg';

class View extends Component {
    render() {
        return (
            <div className="App">

                <img src={logo} className="App-dart" alt="logo" />

                <div className="Button">
                    <button onClick="">Start</button>
                    <button>Stop</button>
                    <button>Wait</button>
                    <button>Reset</button>
                </div>

            </div>
        )
    }
}

export default View